import { isMobileAndTablet } from "lib/userAgent";
import { PaymentAddress } from "lib/api";
import { toast } from "lib/toast";

const useTrustWallet = () => {
  const isAvailable = (currency: PaymentAddress) => resolveTrustWalletAsset(currency.blockchain, currency.token, currency.contract) !== null
    && isMobileAndTablet();

  return {
    isAvailable,
    pay: async (address: PaymentAddress) => {
      if (isAvailable(address) === false) {
        toast("Trust Wallet is not support for current platform or currency");
        return;
      }

      const asset = resolveTrustWalletAsset(address.blockchain, address.token, address.contract);
      if (asset === null) {
        toast("Trust Wallet is not support for current platform or currency");
        return;
      }

      const base = "https://link.trustwallet.com/send";
      window.location.href = `${base}?asset=${asset}&address=${address.address}&amount=${address.displayAmount}`;
    }
  };
};

const resolveTrustWalletAsset = (blockchain: string, token: string, contract: string) => {
  switch (true) {
    case blockchain === "tron" && token === "TRX":
      return "c195";

    case blockchain === "tron" && token === "TRC20_USDT":
      return `c195_t${contract}`; // USDT ticker id

    case blockchain === "tron" && token === "TRC20_USDC":
      return `c195_t${contract}`; // USDC ticker id

    case blockchain === "near":
      return "c397"; // NEAR ticker id

    default:
      return null;
  }
};

export default useTrustWallet;
